.rbc-event-label {
  display: none;
}

#wrapper {
  padding: 88px 0px;
}
.navbar-expand-lg .navbar-nav.side-nav {
  -ms-flex-direction: column;
      flex-direction: column;
}
.header-top {
  -webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

@media (min-width: 992px) {
  .navbar-nav.side-nav {
    text-align: center;
    background: #585f66;
    -webkit-box-shadow: 2px 1px 2px rgba(0, 0, 0, 0.1);
            box-shadow: 2px 1px 2px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 57px;
    -ms-flex-direction: column !important;
        flex-direction: column !important;
    left: 0px;
    width: 70px;
    bottom: 0;
    padding-bottom: 40px;
  }
  .side-nav li {
    border-bottom: 1px solid #50575d;
  }
  .side-nav li a {
    padding: 20px;
    display: block;
    position: relative;
  }
  .navbar-nav li a svg {
    font-size: 25px;
    margin: 0 10px 0 5px;
  }
  .navbar-nav li a .ttip {
    opacity: 0;
    position: absolute;
    background: #333;
    left: 0px;
    white-space: nowrap;
    top: 16px;
    border-radius: 3px;
    padding: 5px 10px;
    -webkit-transition: 0.5s ease all;
    -o-transition: 0.5s ease all;
    transition: 0.5s ease all;
  }
  .navbar-nav li a:hover .ttip {
    opacity: 1;
    left: 70px;
  }
  .navbar-nav li a .ttip:after {
    right: 100%;
    top: 50%;
    border: solid transparent;
    content: ' ';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(136, 183, 213, 0);
    border-right-color: #333;
    border-width: 5px;
    margin-top: -5px;
  }
}
.tab-pane {
  padding: 20px;
}

.nav-tabs > li > a,
.nav-pills > li > a {
  margin-left: 25px;
  margin-right: 27px;
}
.modal-container {
  position: relative;
}
.modal-container .modal,
.modal-container .modal-backdrop {
  position: absolute;
}
.fade:not {
  opacity: 1;
  -webkit-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
}
.box {
  padding: 10px;
  margin: 4px;
  float: left;

  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);

          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);

  background: #fff;
  border-radius: 2px;
  display: inline-block;
  min-height: 100px;
  margin: 1rem;
  position: relative;
  width: 300px;
}
.card2 {
  display: inline-block;
  background: #e2e1e0;
  padding: 0 12px;
  font-size: 13px;
  padding: 10px;
  margin: 4px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2);
}



