img {
  width: 100%;
}

.landing {
  background: url('./img/showcase.jpg') no-repeat;
  background-size: cover;
  background-position: center;

  position: relative;
  margin-top: -32px;
  margin-bottom: -50px;
}
.landing-inner {
  padding-top: 5px;
}

.rbc-event-label {
  display: none;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control:-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}
.modal-container {
  position: relative;
}
.modal-container .modal,
.modal-container .modal-backdrop {
  position: absolute;
}



